
<style scoped>
</style>


<template>
  <div class="mylesson">
    <van-nav-bar
      title="我的学习"
      right-text="目"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />

    <div class="card" v-if="webcard&&weblist">
      <van-tabs  v-model="lessonindex" class="titlist">
        <van-tab
          v-for="(m,i) in webcard"
          :key="i"
        >
        <template #title>
            <div  @click="changetype(i,m.type)">
            {{ m.name+'('+m.num+')' }}
            </div>
        </template>
            <ol class="cttlist" v-if="nav_type==m.type">
                <li v-for="(sm,si) in weblist" :key="si">
                <div>{{ sm.title }}</div>
                <div>
                    {{ term(sm.start_play_date,sm.end_play_date) }}
                    共{{ sm.section_num }}课时

                </div>
                <div>{{ '学习进度'+sm.progress_rate+'%' }}</div>
                </li>
            </ol>
        </van-tab>
      </van-tabs>
      <!-- <ul class="titlist">
                <li  v-for='(m,i) in carddata' :key='i'
                    @click="lessonindex=i"
                >
                    {{ m.name+'('+m.list.length+')' }}
                </li>
      </ul>-->
      <!-- <ol class="cttlist">
        <li v-for="(m,i) in carddata[lessonindex].list" :key="i">
          <div>{{ m.lesson }}</div>
          <div>{{ m.term }}</div>
          <div>{{ '学习进度'+m.learnstep+'%' }}</div>
        </li>
      </ol> -->
    </div>
  </div>
</template>

v-for='(m,i) in carddata[lessonindex].list' :key='i'
<script>
export default {
  data() {
    return {
      nav_type: 2,
      webcard:null,
      weblist:null,
      carddata: [
        {
          name: "直播课",
          list: [{ lesson: "开课啦", term: "猴年马月", learnstep: 0 }]
        },
        { name: "点播课", list: [] },
        { name: "音频课", list: [] },
        { name: "图文课", list: [] },
        { name: "面授课", list: [] },
        { name: "会员课", list: [] }
      ],
      lessonindex: 0
    };
  },
  created() {},
  mounted() {
    this.get_nav_title();
  },
  //props:{},//[]
  methods: {
      async vanclick(name,title,asd){
          console.log(name,title,asd)
      },
    async changetype(i,type){
        this.lessonindex=i
        this.nav_type=type
        let {data:{data:res}}=await this.$http.get(
            `http://120.53.31.103:84/api/app/myStudy/${type}`
        )
        console.log(res)
        this.weblist=res.courseList
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      this.$router.push({
        path: "/rili",
        query: {}
      });
    },
    async get_nav_title() {
      let {
        data: { data: res }
      } = await this.$http.get(`http://120.53.31.103:84/api/app/myStudy/${this.nav_type}`, {
        params: {}
      });
    //   console.log(res);
      this.webcard=res.typeNum
      this.weblist=res.courseList

    }
  },
  computed: {
      term(){
          return (t1,t2)=>{
            //   console.log(t1,t2)
              let tarr=[t1,t2].map(m=>{
                  return new Date(m+'000'-0).toLocaleString()
              })
            //   console.log(tarr)
              return tarr.join(' - ')
          }
      }
  },
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>