<template>
  <div id="concem">
    <!-- 我的（模态框）*+12* 页面+个人信息+设置+余额明细  -->
    <van-nav-bar title="我的收藏" left-arrow @click-left="onClickLeft" />

    <div class="list" v-if='list'>
        <div class="cell" v-for='(m,i) in list' :key='i'>
          <ldcell :m='m'></ldcell>
        </div>
    </div>
    <div class="empty" v-show='!list'>
      <img  src="../assets/pic/nothing-heart.png" alt />
      <p>暂无收藏</p>
    </div>
  </div>
</template>

<script>
import ldcell from '@/components/ldcell'

export default {
  name: "",
  data() {
    return {
        list:null,
    };
  },
  props: {},
  created() {},
  mounted() {
      this.initialdata()
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    async initialdata() {
        let {data:res}= await this.$http.get(
            `http://120.53.31.103:84/api/app/collect`,{params:{
                page:1,
                limit:10,
                type:1,
            }}
        )
        console.log(res)
        let {data}=res
        this.list=(data.list&&data.list.length)?data.list:null;
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {
    ldcell,
  }
};
</script>

<style scoped lang="less">
#concem {
  height: 100%;
  background: #f0f2f5;
}
.empty {
  margin-bottom: 0.5rem;
  > img {
    width: 4.5rem;
    height: 4.5rem;
  }
  > p {
    font-size: 0.3rem;
    color: #8c8c8c;
    margin-top: 0.2rem;
  }
}
</style>
