
<style scoped lang='scss'>
// 本页修改项修改面板
.decide {
  &-avatar {
    &-camera {
          padding: 1.33333vw 4vw;
          background: #fff;
          height: 12.26667vw;
          line-height: 12.26667vw;
          font-size: 4.53333vw;
          font-weight: 300;
          color: #030303;
          text-align: center;
          display: block;
          justify-content: space-between;
          position: relative;
          input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            color: inherit;
            font: inherit;
          }
          &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 1px;
            background-color: #f5f5f5;
          }
    }
    &-local {
          padding: 1.33333vw 4vw;
          background: #fff;
          height: 12.26667vw;
          line-height: 12.26667vw;
          font-size: 4.53333vw;
          font-weight: 300;
          color: #030303;
          text-align: center;
          display: block;
          justify-content: space-between;
          position: relative;
          input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            color: inherit;
            font: inherit;
          }
          &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 1px;
            background-color: #f5f5f5;
          }
    }
  }
  &-grade {

  }
  &-birth {

  }
  &-area {

  }
}
.avatar-setter {
    background: rgba($color: #000000, $alpha: 0.5);
}
.infosetterbox {
  height: 100%;
  background: #f0f2f5;
}
.infolist {
  background: #fff;
  margin: 2.66667vw 0;
  padding: 1.33333vw 4vw;

  &-cell {
    height: 14.66667vw;
    line-height: 14.66667vw;
    display: flex;
    justify-content: space-between;
    position: relative;
    &:not(.mobile):after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%) rotate(45deg);
      width: 2.66667vw;
      height: 2.66667vw;
      border-top: 1px solid #b7b7b7;
      border-right: 1px solid #b7b7b7;
    }
    span {
      &.one {
        font-size: 3.73333vw;
        color: #595959;
      }
      &.two {
        font-size: 3.73333vw;
        color: #8c8c8c;
        margin-right: 6.66667vw;
      }
      img {
        width: 9.86667vw;
        height: 9.86667vw;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
  }
}
</style>


<template>
  <div class="infosetterbox">
    <van-nav-bar title="个人信息" left-arrow @click-left="onClickLeft" />
    <ul class="infolist" v-if="info">
      <li class="infolist-cell" @click="setinfo('头像')">
        <span class="one">头像</span>
        <span class="two">
          <img :src="info.avatar" />
        </span>
      </li>
      <li class="infolist-cell" @click="setinfo('昵称')">
        <span class="one">昵称</span>
        <span class="two">{{ info.nickname }}</span>
      </li>
      <li class="infolist-cell mobile">
        <span class="one">手机号</span>
        <span class="two">{{ info.mobile }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('性别')">
        <span class="one">性别</span>
        <span class="two">{{ info.sex==3?'保密':info.sex?'女':'男' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('出生日期')">
        <span class="one">出生日期</span>
        <span
          class="two"
        >{{ info.birthday?info.birthday:'请选择' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('所在城市')">
        <span class="one">所在城市</span>
        <span
          class="two"
        >{{ [info.province_name,info.city_name,info.district_name].filter(m=>{return m.trim()}).join(',')?'有':'请选择' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('学科')">
        <span class="one">学科</span>
        <span class="two">{{ attr&&attr.length?attr[0].attr_value:'请选择' }}</span>
      </li>
      <li class="infolist-cell" @click="setinfo('年级')">
        <span class="one">年级</span>
        <span class="two">{{ attr&&attr.length?attr[1].attr_value:'请选择' }}</span>
      </li>
    </ul>
    <!-- 修改头像 -->
    <van-action-sheet 
      v-model="rise._avatar" 
      cancel-text="取消"  
      class="decide-avatar"
    >
        <div class="decide-avatar-camera">
            拍照
            <input 
              @change="uploadimg('c',$event)" 
              ref="fromcamera"
              type="file"
               accept="image/*" 
              capture="camera" 
            />
        </div>
        <div class="decide-avatar-local">
            从手机相册选择
            <input
              @change="uploadimg('l',$event)" 
              type="file" 
              accept="image/*" 
              ref="fromlocal"
            />
        </div>
    </van-action-sheet>

    <!-- 出生日期 -->
    <van-action-sheet
        v-model="rise._birth"
      class="decide"
    >
        <van-datetime-picker
            v-model="birthdata"
            type="date"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm='decide_birth'
        />
    </van-action-sheet>


    <!-- 年级 -->
        <van-action-sheet
        v-model="rise._grade"
        cancel-text="取消"
        close-on-click-action
        @cancel="onCancel"
      class="decide-grade"
    >
      <van-picker
        title="标题"
        show-toolbar
        :columns="picker_options._grade"
        @confirm="grade_confirm"
        @cancel="grade_cancel"
        @change="grade_change"
      ></van-picker>
      </van-action-sheet>

    <!-- 所在城市 -->
    <van-action-sheet
      v-model="rise._area"
      cancel-text="取消"
      close-on-click-action
      @cancel="onCancel"
      class="decide"
    >
      <!-- 省市区 -->
      <van-area title="标题" :area-list="areaList" />
    </van-action-sheet>

  </div>
</template>

v-for='(m,i) in attr?attr:[]' :key='i'
<script>
import area from "@/util/data_mod/area";
export default {
  data() {
    return {
      local_img:null,
      // 选择器
      picker_options: {
        _avatar: ["杭州", "宁波", "温州", "嘉兴", "湖州"],
        _grade: ["杭州", "宁波", "温州", "嘉兴", "湖州"]
      },
      // 动作面板
      rise: {
        _avatar: false,
        _birth: false,
        _area: false,
        _grade: false
      },
      actions: [{ name: "选项一" }, { name: "选项二" }, { name: "选项三" }],
      //   省市区三级联动
      areaList: area,
      // 遮罩层
      show: false,
      // 时间拾取
      minDate: new Date(1970, 0, 1),
      maxDate: new Date(2025,11,30),
      birthdata: new Date(),
      info: null,
      attr: null,
      empty_attr: [
        { attr: "学科", attr_value: "" },
        { attr: "年级", attr_value: "" }
      ],
      infobox:{
        avatar:'',
        birthday:'',

      }
    };
  },
  created() {},
  mounted() {
    this.getuserinfo();
      this.getuserattr()
  },
  //props:{},//[]
  methods: {
    // 确认出生日期
    async decide_birth(v){
      let birthday=[
        v.getFullYear(),
        v.getMonth()+1,
        v.getDate()
      ].map(m=>{return (''+m).padStart(2,'0')}).join('-')
      this.infobox.birthday=birthday
      console.log(birthday)
      let {data}=await this.$http.put(
        `http://120.53.31.103:84/api/app/user`,{
          birthday
        }
      )
      console.log(data)
    this.getuserinfo();
    },
    uploadimg(key,e=event){
      this.local_img=e.target.files[0]
      let type=this.local_img.type.split('/')[0]
      if(type=='image'){
        let reader=new FileReader()
        reader.readAsDataURL(this.local_img)
        reader.onloadend=()=>{
          let dataURL=reader.result;
          let blob=this.dataURItoBlob(dataURL)
          this.upload_file(blob)
        }
      }else{
        console.log('这玩意儿不是图片')
      }
      this.rise._avatar=false
    },
    dataURItoBlob(dataURI){
      let byteString=atob(dataURI.split(',')[1]);
      let mimeString=dataURI.split(',')[0].split(':')[1].split(';')[0]
      let ab=new ArrayBuffer(byteString.length);
      let ia=new Uint8Array(ab)
      for(let i=0;i<byteString.length;i++){
        ia[i]=byteString.charCodeAt(i)
      }
      return new Blob([ab],{type:mimeString})
    },
    async upload_file(imgUrl){
      let formdata=new FormData()
      formdata.append('file',imgUrl)
      let {data:res}=await this.$http.post(
        `http://120.53.31.103:84/api/app/public/img`,formdata,{
          headers:{
            'Content-Type':'multipart/form-data'
          }
        }
      )
      
      console.log(res)
      this.infobox.avatar=res.data.path
      let {data}=await this.$http.put(
        `http://120.53.31.103:84/api/app/user`,{
          avatar:res.data.path
        }
      )
      console.log(data)
      this.getuserinfo();
    },
    grade_confirm() {
      console.log("确认 年级 选项");
    },
    grade_cancel() {
      console.log("取消 年级 选项");
    },
    grade_change() {
      console.log("修改 年级 选项");
    },
    onCancel() {},
    onClickLeft() {
      this.$router.go(-1);
    },
    setinfo(keystr) {
      switch (keystr) {
        case "头像":
          console.log("本页修改 " + keystr);
          this.rise._avatar = true;
          break;
        case "出生日期":
          console.log("本页修改 " + keystr);
          this.rise._birth = true;
          break;
        case "所在城市":
          console.log("本页修改 " + keystr);
          this.rise._area = true;
          break;
        case "年级":
          console.log("本页修改 " + keystr);
          this.rise._grade = true;
          break;
        default:
          console.log("跳转修改页面 " + keystr);
          return;
          break;
      }
    },
    async getuserinfo() {
      let {
        data: { data: res }
      } = await this.$http.get(`http://120.53.31.103:84/api/app/userInfo`, {
        params: {}
      });
      // console.log(res)
      this.info = res;
      this.attr = res.attr;
      console.log(this.info, this.attr);
    },
    async getuserattr(){
      let {data:{data:res}}=await this.$http.get(
        `http://120.53.31.103:84/api/app/module/attribute/1?`,{
          params:{}
        }
      )
      console.log(res)
    }
  },
  computed: {},
  watch: {},
  filters: {},
  directives: {},
  components: {}
};
</script>