<template>
  <div id="app">
    <div class="zjyheader">
      <div>
        <van-icon name="arrow-left" @click="onback" />
      </div>
      <div>
        <input class="zjyIn" type="text" placeholder="请输入内容"  v-model="keywords"/>
      </div>
      <div @click="runsearch">搜索</div>
    </div>
    <div class="zjyHis">
      <div class="p">历史搜索</div>
      <div class="p" @click="clearseek">
        <img src="http://t7.baidu.com/it/u=2566816517,3864068751&fm=193" alt />
      </div>
    </div>
      <ul v-if="seek_history&&seek_history.length">
        <li v-for='(m,i) in seek_history' :key='i'>
          {{ m }}
        </li>
      </ul>
    <div class="mainarea" v-if="seeklist">
      <ldcell 
        v-for='(m,i) in seeklist' :key='i'
        :m='m'
      ></ldcell>
    </div>
  </div>
</template>


<script>
import seekeyword from '@/util/data_mod/seek_keyword'
import Vue from 'vue'
import ldcell from '@/components/ldcell'
Vue.component('ldcell',ldcell)
export default {
  data() {
    return {
      keywords:'',
      seeklist:null,
      records:[],
      seek_history:null,
    };
  },
  created() {},
  mounted() {
        let lsh=localStorage.seek_history
        if(lsh){
          this.seek_history=JSON.parse(lsh)
        }else{
           this.seek_history=[]
        }
  },
  methods: {
    clearseek(){
       localStorage.seek_history=''
       this.seek_history=[]
    },
    onback(){
      this.$router.go(-1)
    },
    switchtip(){
      return seekeyword._seek.data.list
    },
    async runsearch(){
      if(this.keywords.trim()){
         this.seek_history.push(this.keywords)
         localStorage.seek_history=JSON.stringify(this.seek_history)

      }
      let {data:res}=await this.$http.get(
        `http://120.53.31.103:84/api/app/courseBasis`,{params:{
          limit:10,
          page:1,
          course_type:0,
          keywords:this.keywords,
        }}
      )
      let {data}=res
      console.log(data)
      this.seeklist=data.list.length?data.list:this.switchtip()
    }
  }
};
</script>


<style scoped lang="scss">
.zjyheader {
  width: 100%;
  height: 1.1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 0.3rem;
}


.zjyHis > .p > img {
  width: 0.4rem;
  height: 0.35rem;
}
.zjyHis > .p:nth-of-type(1){
  font-weight: bold;
}
.zjyIn {
  background: #e4e7ed;
  border: none;
  border-radius: 3rem;
  padding: 0.2rem 0.8rem;
}
.zjyHis {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.3rem;
}
.zjyheader > div:nth-of-type(3) {
  color: #e0e0df;
}
</style>