<template>
  <div class="index">
    <!-- swiper -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) of banner" :key="index">
        <img :src="item.banner_img" alt />
      </van-swipe-item>
    </van-swipe>

    <!-- nav -->
    <ul class="nav">
      <li v-jump="path1">
        <img src="../assets/pic/characteristic.png" alt />
        <font>特色课</font>
      </li>
      <li v-jump="path2">
        <img src="../assets/pic/coach.png" alt />
        <font>一对一辅导</font>
      </li>
      <li v-jump="path3">
        <img src="../assets/pic/Study-Calendar.png" alt />
        <font>学习日历</font>
      </li>
    </ul>

    <!-- 四个页面 -->

    <section v-for="(item,index) of indexList" :key="index">
      <!-- 名师阵容 -->
      <div v-if="item.channel_info.type == 3">
        <p class="op-title">{{ item.channel_info.name }}</p>
        <ul class="oto-content">
          <li class="oc-item" v-for="(v,i) of item.list" :key="i" @click="toggle1(v,i)">
            <img :src="v.teacher_avatar" alt />
            <div>
              <p>{{ v.teacher_name }}</p>
              <p>{{ v.introduction }}</p>
            </div>
          </li>
        </ul>
      </div>
      <!-- 精品课堂 -->
      <div v-if="item.channel_info.type == 1" >
        <p class="op-title">{{ item.channel_info.name }}</p>
        <div class="index-item-page">
          <ldcell
            class="ii-item" 
            v-for="(v,i) of item.list" :key="i" 
            :m='v'
          ></ldcell>
        </div>
      </div>
      
      <!-- 明星讲师 -->
      <div v-if="item.channel_info.type == 4">
        <p class="op-title">{{ item.channel_info.name }}</p>
        <ul class="oto-content">
          <li class="oc-item" v-for="(v,i) of item.list" :key="i" @click="toggle1(v,i)">
            <img :src="v.teacher_avatar" alt />
            <div>
              <p>
                <span>{{ v.teacher_name }}</span>
                <font>{{ v.level_name }}</font>
              </p>
              <p>{{ v.introduction }}</p>
            </div>
          </li>
        </ul>
      </div>
    </section>
    <!-- footer -->
    <zzFooter></zzFooter>
    <!-- <wylIcon></wylIcon> -->
  </div>
</template>

<script>
// 引入小icon留言
// import wylIcon from "../components/dragball"
// 引入底部导航
import zzFooter from "../components/footbar";
import $ from 'jquery'

export default {
  name: "",
  data() {
    return {
      banner: [],
      indexList: [],

      //上面 三个（nav） 定位  的 页面跳转   使用时的  v-jump
      path1: "/WylCC", //特色课  wyl
      path2: "/zjy", //一对一辅导  zjy
      path3: "/rili", // 日历   zjy
      // 四个模块
      path4: "/xiangqing", //名师详情  zjy
    };
  },
  props: {},
  created() {
    this.ajaxBannerList();
    this.ajaxIndexList();
    // this.trygithubjson()
  },
  mounted() {},
  methods: {
    // aimtodetail(bid){
    //   console.log(bid)
    //   console.log('首页跳转壳程详情')
    //   this.$router.push({path:'/WylCD',query:{bid}})
    // },
    async ajaxBannerList() {
      let { data: res } = await this.$http.get(
        "http://120.53.31.103:84/api/app/banner"
      );
      this.banner = res.data;
    },
    async ajaxIndexList() {
      let { data: res } = await this.$http.get(
        "http://120.53.31.103:84/api/app/recommend/appIndex"
      );
      // console.log(res.data);
      this.indexList = res.data;
      console.log(this.indexList)
    },

    // 名师阵容  跳转到  讲师详情
    toggle1(i, a) {
      // console.log(i[a])
      this.$router.push({
        path: "/xiangqing", //zjy
        query: {
          teacher_id: i.teacher_id
        }
      });
    },
    // trygithubjson(){
    //   // let {data:res}=await this.$http.get(
    //   //   `/test/%E6%B5%8B%E8%AF%95%E6%95%B0%E6%8D%AE/index.json`,{params:{

    //   //   }}
    //   // )
    //  $.get(
    //     'https://hellarose.github.io/%E6%B5%8B%E8%AF%95%E6%95%B0%E6%8D%AE/index.json',
    //     res=>{
    //   console.log(res)
    //     }
    //   )
    // }
  },
  computed: {},
  watch: {},
  filters: {
    much(val) {
      return (val / 100).toFixed(2);
    }
  },
  directives: {},
  components: {
    zzFooter
    // wylIcon,
  }
};
</script>
<style scoped lang="scss">
img {
  vertical-align: middle;
}
.index {
  background: #f0f2f5;
  height: 740%;
}
.my-swipe .van-swipe-item {
  height: 4.5rem;
  > img {
    width: 100%;
    height: 100%;
  }
}
.nav {
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: relative;
  top: -0.5rem;
  > li {
    width: 25%;
    border-radius: 0.15rem;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    height: 1.8rem;
    font-size: 0.3rem;
    flex-direction: column;
    background: #fff;
    text-align: center;
    > img {
      width: 0.55rem;
    }
    > font {
      font-size: 0.2rem;
      margin-top: 0.2rem;
      color: #8c8c8c;
    }
  }
}
p {
  margin: 0;
}
.op-title {
  margin-left: 0.35rem;
  border-left: 0.07rem solid #eb6100;
  font-size: 0.3rem;
  color: #595959;
  text-align: left;
  padding-left: 0.25rem;
  margin-bottom: 0.3rem;
}
.oto-content {
  padding: 0.1rem 0.2rem 0.5rem;
  .oc-item {
    height: 2.15rem;
    background: #fff;
    border-radius: 0.15rem;
    display: flex;
    align-items: center;
    padding: 0 0.4rem;
    margin-top: 0.2rem;
    > img {
      width: 1.02rem;
      height: 1.02rem;
      border-radius: 50%;
    }
    > div {
      flex: 1;
      margin-left: 0.2rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      p {
        display: block;
        text-align: left;
      }
      > p:nth-of-type(1) {
        font-size: 0.28rem;
        color: #000;
      }
      > p:nth-of-type(2) {
        font-size: 0.26rem;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}
.index-item-page {
  padding: 0.4rem;
  
}
</style>