<template>
  <div class="wyl_big1">
    <div class="_before" ref='fake'></div>
    <div class="wyl_big1-header wyl_header">
      <van-nav-bar
        class="wyl_icon"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      >
        <template #title>
          <div class="title">
            <ul v-if="changehead" class="title-true">
              <li class="title-true-ld"
                @click="scrtowards('lesson_introduce')"
              >课程介绍</li>
              <li class="title-true-ls"
                @click="scrtowards('lesson_schedule')"
              >课程大纲</li>
              <li class="title-true-lc"
                @click="scrtowards('lesson_comment')"
              >课程评论</li>
            </ul>
            <div v-else class="title-false">课程详情</div>
          </div>
        </template>
        <template #right>
          <van-icon name="qr-invalid" size="18" />
        </template>
      </van-nav-bar>

      <van-overlay :show="show" @click="show=false" class="overlay">
        <div class="wrapper">
          <div class="block" @click.stop>
            <qriously class="canbox" :value="initQCode" :size="70" />
          </div>
        </div>
      </van-overlay>
    </div>
    <div class="cd-body">
      <!-- main1 -->
      <div class="cd-info" v-if="info">
        <p class="info-title">{{ info.title }}</p>
        <!-- 收藏 -->
        <i 
          @click="coll" 
          :class="{
            'info-collect':1,
            'active':info.is_collect
          }"
        ></i>
        <p class="info-price cd-free">{{ !info.is_free?((info.price/100).toFixed(2)):'免费' }}</p>
        <p class="info-price">
          <img
            src="@/assets/img/1-after.png"
            class="price-ico"
          />
          <font>1.00</font>
        </p>
        <div>
          <p class="info-com">共{{ info.total_periods }}课时 | {{ info.sales_num }}人已报名</p>
          <p class="info-com">
            开课时间：
            {{ info.start_play_date | nowtime(info.start_play_year) }}
            -
            {{ info.end_play_date | nowtime(info.start_play_year) }}
          </p>
        </div>
      </div>
      <!-- main2 -->
      <div class="cd-teachers com-style" v-if="teachers">
        <p class="cd-title">教学团队</p>
        <ul>
          <li 
            v-for='(m,i) in teachers' :key='i'
          >
            <img :src="m.teacher_avatar" />
            <span>{{ m.teacher_name }}</span>
          </li>
        </ul>
      </div>
      <!-- main3-->
      <div id="tro" class="cd-tro com-style" ref="lesson_introduce">
        <p class="cd-title">课程介绍</p>
        <div class="cd-details">
          <div v-if="info" v-html="info.course_details"></div>
        </div>
      </div>

      <!-- main4 -->
      <div id="list" class="cd-list com-style" ref="lesson_schedule">
        <p class="cd-title">课程大纲</p>
        <div class="charpter" v-if="chapter">
          <div v-for='(m,i) in chapter' :key='i'>
            <section class="period" 
                v-for='(sm,si) in m.child' :key='si'
            >
              <div class="charp-title">
                <span class="huifang">回放</span>
                {{ m.title+' '+sm.periods_title }}
              </div>
              <div class="charp-time">
                <span v-for='(ssm,ssi) in sm.teachers' :key='ssi'>
                  {{ ssm.teacher_name }}
                </span>
                <span>{{ sm.start_play+' - '+sm.end_play.slice(sm.end_play.length-9) }}</span>
              </div>
            </section>
          </div>
        </div>
        <!---->
      </div>

      <!-- main5 -->
      <div id="com" class="cd-comment com-style" ref="lesson_comment">
        <p class="cd-title">课程评论</p>

        <div class="com-item-page" >
          <!-- one  list -->
          <div role="feed" class="van-list" v-if="comments">
            <ul>
              <li>
                <div class="evaluate-top">
                  <img
                    src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20206kRSVtmcMX1589444592.jpg"
                  />
                  <div class="evaluate-t-left">
                    <div class="info">
                      <span>15910732068</span>
                      <van-rate
                        class="van-rate"
                        v-model="value"
                        :size="15"
                        color="#ffd21e"
                        void-icon="star"
                        void-color="#eee"
                        :gutter="0.2"
                      />

                      <span class="times">2020-04-23 14:56</span>
                    </div>
                    <!---->
                    <div class="evaluate-btom">very go o g</div>
                  </div>
                </div>
              </li>
              <li>
                <div class="evaluate-top">
                  <img
                    src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20206kRSVtmcMX1589444592.jpg"
                  />
                  <div class="evaluate-t-left">
                    <div class="info">
                      <span>15910732068</span>
                      <van-rate
                        class="van-rate"
                        v-model="value"
                        :size="15"
                        color="#ffd21e"
                        void-icon="star"
                        void-color="#eee"
                        :gutter="0.2"
                      />

                      <span class="times">2020-04-23 14:56</span>
                    </div>
                    <!---->
                    <div class="evaluate-btom">very go o g</div>
                  </div>
                </div>
              </li>
            </ul>
            <div class="van-list__placeholder"></div>
          </div>

          <!-- two empty -->
          <div class="empty" v-if='!comments'>
            <img src="../../assets/record.png" />
            <p data-v-0fa74510>暂无评论</p>
          </div>
        </div>
      </div>
    </div>
    
  <div class="wyl-footer">
    <button class="course-btn van-button" @click="skipto">
      <span class="van-button__text"
        v-if="info"
      >{{ info.is_buy?'立即学习':'立即报名' }}</span>
    </button>
  </div>
  </div>
</template>

<script>
import datamod from '@/util/data_mod/course_basis'
export default {
  name: "",
  data() {
    return {
      initQCode:'',
      show: false,
      value: 0,
      info: null,
      teachers: null,
      bid: "",
      changehead: false,
      chapter:null,
      comments:null
    };
  },
  // props: ['bid'],
  components: {},
  mounted() {
    window.addEventListener("scroll", this.vscroll, false);
    let bid = this.$route.query.bid;
    this.bid = bid;
    // console.log(this.bid);
    this.initialdata(this.bid);
    this.getchapter()
    this.getcomment()
  },
  methods: {
    async getcomment(){
      let {data:res}=await this.$http.post(
        `http://120.53.31.103:84/api/app/courseComment`,{
          id:this.bid
        }
      )
      let {data}=res
      console.log('comment',data)
      if(data&&data.length){
        this.comments=data.list
      }
    },
    async getchapter(){
      let {data:res}=await this.$http.post(
        `http://120.53.31.103:84/api/app/courseChapter`,{
          id:this.bid
        }
      )
      let {data}=res
      this.chapter=data
      console.log('chapter',data)
    },
    async skipto(){
      if(!this.info.is_buy){
        let {data:res}= await this.$http.post(
          `http://120.53.31.103:84/api/app/order/downOrder`,{
            type:this.info.course_type,
            shop_id: this.info.id,
          }
        )
        this.info.is_buy=1
      }else{
        this.$router.push({path:'/mystudy',query:{bid:this.bid}})
      }
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    async onClickRight() {
      // this.$router.push("/zjySearch");
      let {data:res}=await this.$http.get(
        `http://120.53.31.103:84/api/app/share/${this.bid}`
      )
      let {data}=res
      this.initQCode=data.url
      this.show = true;
    },
    getElementToPageTop(el) {
      if (el.parentElement) {
        return this.getElementToPageTop(el.parentElement) + el.offsetTop;
      }
      return el.offsetTop;
    },
    scrtowards(str){
      let docel = document.documentElement;
      let trs=this.$refs
      let gt_fk=trs.fake.clientHeight
      docel.scrollTop=this.getElementToPageTop(trs[str])-gt_fk;
    },
    vscroll() {
      let docel = document.documentElement;
      let trs=this.$refs
      let ld = trs.lesson_introduce;
      let gt_ld = this.getElementToPageTop(ld);
      let gt_fk=trs.fake.clientHeight
      if (docel.scrollTop >= (gt_ld-gt_fk)) {
        this.changehead = true;
      }else {
        this.changehead = false;
      }
    },
    async initialdata(bid) {
      let { data: res } = await this.$http.get(
        `http://120.53.31.103:84/api/app/courseInfo/basis_id=${bid}`
      );
      let { data } = res;
      if(!res.data){
        res.data=datamod._error
      }
      this.info = data.info;
      this.teachers = data.teachers;
      console.log( 'info',this.info,'teachers', this.teachers);
    },
    async coll() {
      let {data:res}= await this.$http
        .post(`http://120.53.31.103:84/api/app/collect`, {
          course_basis_id: this.info.id,
          type: 1,
        })
          this.info.is_collect=Math.abs(this.info.is_collect-1)
    }
  },
  filters: {
    nowtime(t, y) {
      let time = new Date(t),
        tarr = [
          time.getFullYear(),
          time.getMonth() + 1,
          time.getDate(),
          time.getHours(),
          time.getMinutes(),
          time.getSeconds()
        ].map(function(m) {
          return ("" + m).padStart(2, "0");
        }),
        str = y + "." + tarr[1] + "." + tarr[2] + " " + tarr[3] + ":" + tarr[4];
      return str;
    },
    
  }
};
</script>

<style scoped lang='scss'>
.wyl_big1 {
  ._before {
    
    height: 1.17rem;
  }
  &-header {
    // background: red; 
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;

    height: 1.17rem;
    line-height: 1.17rem;
    border-bottom: 0.02rem solid #f0f2f5;
    .wyl_icon {
      width: 100%;
      height: 1.17rem;
      box-sizing: border-box;
      .title {
        &-true {
          display: flex;
          justify-content: space-around;
          align-items: center;
          &>li {
            padding: 0 0.1rem;
          }
        }
      }
    }
    .overlay {
      right: 0;
      bottom: 0;
      .wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      .block {
      width: 7rem;
      height: 7rem;
        .canbox {
          height: 100%;
          canvas {
            width: 100% !important;
            height:100% !important;
          }
        }
      }
    }
    }

    .block {
      width: 1.3rem;
      height: 1.3rem;
      background-color: #fff;
      img {
        width: 1.3rem;
        height: 1.3rem;
      }
    }
  }
  background: red;
  min-height: 5rem;
  box-sizing: border-box;
  // main
  .cd-body {
    text-align: left;
    background: #f0f2f5;
    padding-bottom: 0.4rem;
    box-sizing: border-box;

    // main1
    .cd-info {
      // width: 100%;
      padding: 0.25rem;
      background: #fff;
      position: relative;

      .info-title {
        font-size: 0.38rem;
        // font-weight: bold;
        color: #333;
        padding-right: 1.06rme;
        line-height: 0.613rem;
      }

      .info-collect {
        position: absolute;
        right: 0.26667rem;
        top: 0.13333rem;
        width: 0.66667rem;
        height: 0.66667rem;
        background: url("../../assets/notclcted.png") no-repeat 50%;
        background-size: 0.48rem 0.48rem;
        &.active {
          background: url("../../assets/clcted.png") no-repeat 50%;
          background-size: 0.48rem 0.48rem;
        }
      }

      .info-price {
        color: #eb6100;
        font-size: 0.4rem;
        img {
          width: 0.24rem;
          height: 0.24rem;
        }
        font {
          font-size: 0.45333rem;
          font-weight: 500;
          color: #ee1f1f;
          line-height: 0.85333rem;
        }
      }

      > div {
        .info-com {
          font-size: 0.32rem;
          font-weight: 400;
          color: rgba(0, 0, 0, 0.45);
          line-height: 0.8rem;
        }
      }
    }

    // main2
    .com-style {
      margin-top: 0.25rem;
      background: #fff;
      padding: 0.13rem 0.26rem;

      .cd-title {
        font-size: 0.32rem;
        font-weight: 500;
        color: #262626;
        padding-top: 0.2rem;
      }
      > ul {
        display: flex;
        flex-wrap: wrap;
        padding: 0.28rem 0;
        li {
          width: 20%;
          height: 1.6rem;
          display: flex;
          align-items: center;
          flex-direction: column;
          box-sizing: border-box;
          img {
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 50%;
          }
          > span {
            padding: 0.21rem 0 0 0.4rem;
            font-size: 0.28rem;
            color: #595959;
          }
        }
      }
    }

    // main3
    .cd-tro {
      margin-top: 0.25rem;
      background: #fff;
      padding: 0.13rem 0.26rem;

      .cd-details {
        font-size: 0.36rem;
        padding: 0.4rem 0.13rem;
        p {
          width: 100%;
          font-size: 0.28rem;
        }
      }
    }

    // main4
    .cd-list {
      .charpter {
        padding: 0.2rem 0.26rem;
        section.period {
          padding: 0.1rem 0 0 0;
          .charp-title {
            position: relative;
            padding-left: 0.4rem;
            line-height: 0.8rem;
            font-size: 0.26rem;
            font-weight: 400;
            color: #595959;
            span.huifang {
              background: #ea7a2f;
              display: inline-block;
              width: 0.8rem;
              margin-right: 0.1rem;
              padding: 0 0.13333rem;
              height: 0.38rem;
              border-radius: 0.05333rem;
              font-size: 0.26rem;
              font-weight: 400;
              color: hsla(0, 0%, 100%, 0.85);
              line-height: 0.38rem;
              text-align: center;
            }
          }

          .charp-title:after {
            content: "";
            position: absolute;
            left: 0.05333rem;
            left: 0.53333vw;
            top: 50%;
            transform: translateY(-50%);
            width: 0.10667rem;
            width: 1.06667vw;
            height: 0.10667rem;
            height: 1.06667vw;
            border-radius: 50%;
            background: #eb6100;
          }

          .charp-time {
            font-size: 0.26rem;
            color: #8c8c8c;
            padding-left: 0.13rem;
            padding-bottom: 0.2rem;
            > span {
              padding-left: 0.2rem;
            }
          }
        }
      }
    }

    // main5
    .cd-comment {
      .com-item-page {
        //   one  list
        .van-list {
          // one1
          ul {
            list-style: none;

            li {
              padding: 0.2rem 0.2rem;
              .evaluate-top:nth-last-of-type(1) {
                border: 0;
              }
              .evaluate-top {
                display: flex;
                > img {
                  width: 0.6rem;
                  height: 0.6rem;
                  border-radius: 50%;
                }
                .evaluate-t-left {
                  flex: 1;

                  .info {
                    display: flex;
                    padding-left: 0.26667rem;
                    align-items: center;
                    span:nth-of-type(1) {
                      flex: 1.5;
                      font-size: 0.28rem;
                      font-weight: 400;
                      color: #333;
                      margin-right: 0.1rem;
                    }
                    .vant-rate {
                      flex: 3;
                      height: 0.4rem;
                      box-sizing: border-box;
                      user-select: none;
                    }

                    span.times {
                      //   flex: 2;
                      display: inline-block;
                      font-size: 0.2rem;
                      color: #999;
                      text-align: right;
                    }
                  }
                }
                .evaluate-btom {
                  margin-top: 0.2rem;
                  line-height: 0.4rem;
                  padding-left: 0.27rem;
                  color: #999;
                  font-size: 0.26rem;
                }
              }
            }
          }
          // one2
          .van-list__placeholder {
            height: 0;
            pointer-events: none;
          }
        }

        // two empty
        .empty {
          flex-direction: column;
          display: flex;
          justify-content: center;
          align-items: center;
          img {
            width: 4rem;
            height: 4rem;
          }
          > p {
            font-size: 0.3rem;
            color: #8c8c8c;
            text-align: center;
          }
        }
      }
    }
  }
}

.wyl-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 4;
  background: red;
  .course-btn {
    width: 100%;
    background: #eb6100;
    font-size: 0.38rem;
    font-weight: 400;
    color: #fff;
    border: 0.01rem solid #ebedf0;
    display: inline-block;
  }
}
</style>
